<script setup>
import { ref, onMounted } from 'vue';
import {API_BASE_URL} from "@/config/config";
import MiniTable from "@/components/MiniTable.vue";
import BigTable from "@/components/BigTable.vue";
import MiniForm from "@/components/MiniForm.vue";

import { useUserStore} from "@/stores/user.js";
const userData = useUserStore();

const settings = ref({
   title: "Down Equipment",
   size: [5,55,100],
   canInsert: true,
   canUpdate: true
});

const urls = ref({
  data: `${API_BASE_URL}/de_site`,
  params: `?username=${encodeURIComponent(userData.username)}`,
  dropdowns: {
    vehicle_type: { url: API_BASE_URL + "/dropdowns/vehicle_type", valueField: "vehicle_type", column: "vehicle_type" },
    tk_no: {  url: API_BASE_URL + "/dropdowns/tk_no", valueField: "tk_no", column: "tk_no" },
    tk_no_spare: {  url: API_BASE_URL + "/dropdowns/tk_no", valueField: "tk_no", column: "tk_no" },
    category: {url: API_BASE_URL + "/dropdowns/op_svc_category", valueField: "category", column: "category"},
    engine_: {url: API_BASE_URL + "/dropdowns/op_svc_category_engine", valueField: "engine_", column: "engine_"},
    drive_train: {url: API_BASE_URL + "/dropdowns/op_svc_category_driveTrain", valueField: "drive_train", column: "drive_train"},
    chasis: {url: API_BASE_URL + "/dropdowns/op_svc_category_chasis", valueField: "chasis", column: "chasis"},
    electrical_body: {url: API_BASE_URL + "/dropdowns/op_svc_category_electricalBody", valueField: "electrical_body", column: "electrical_body"},
    ac_system: {url: API_BASE_URL + "/dropdowns/op_svc_category_acSystem", valueField: "ac_system", column: "ac_system"},
    repair_electrical: {url: API_BASE_URL + "/dropdowns/op_svc_category_repairElectrical", valueField: "repair_electrical", column: "repair_electrical"},
    defact: {url: API_BASE_URL + "/dropdowns/op_svc_category_defact", valueField: "defact", column: "defact"},
    body: {url: API_BASE_URL + "/dropdowns/op_svc_category_body", valueField: "body", column: "body"},
  },
  get: {
    vehicle_type: {url: API_BASE_URL + "/get/vehicle_type", valueField: "vehicle_type", column: "vehicle_type"}
  }
});

const fields = ref({
  firstCol: {label: "TK Number", field: "tk_no", type: "type"},
    columns: [
        {column: "tk_no", label: "TK NO", filterText: "Filter TK", required: true,},
        {column: "vehicle_type", label: "Vehicle Type", filterText: "All Vehicle",
          autofill: {
            triggeredBy: "tk_no",
            url: "get/vehicle_type",
            getValue: res => res[0]
          }
        },
        {column: "plate_no", label: "Plate Number", filterText: "All Plate No",
          autofill: {
            triggeredBy: "tk_no",
            url: "get/plate_no",
            getValue: res => res[0]
          }
        },
        {column: "km", label: "Kilometer", filterText: "km", required: true,},
        {column: "down_since", label: "Down Since", filterText: "Down Since", required: true,},
        {column: "estimated_return", label: "Estimated Return"},
        {column: "spare_exists", label: "Unit Spare"},
        {column: "tk_no_spare", label: "TK NO Spare", filterText: "Filter TK", required: true,
          visibleIf: (formData) => formData.spare_exists === true
        },
        {column: "category", label: "Category", filterText: "Category"},
        {column: "schedule", label: "Schedule", filterText: "Schedule", required: true,
          visibleIf: (formData) => {
              return false;
            },
          compute: (formData) => {
            console.log("formdata ", formData.category);
            console.log("some ", formData.category.some(item => item.value === "Schedule"))
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Schedule");
          }
        },
        {column: "engine_", label: "Engine", filterText: "Engine", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Engine");
          }
        },
        {column: "drive_train", label: "Drive Train", filterText: "Drive Train", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Drive Train");
          }
        },
        {column: "chasis", label: "Chasis", filterText: "Chasis", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Chasis");
          }
        },
        {column: "electrical_body", label: "Electrical Body", filterText: "Electrical Body", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Electrical Body");
          }
        },
        {column: "ac_system", label: "AC System", filterText: "AC System", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "AC System");
          }
        },
        {column: "repair_electrical", label: "Repair Electrical", filterText: "Repair Electrical", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Repair Electrical");
          }
        },
        {column: "defact", label: "Defact", filterText: "Defact", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Defact");
          }
        },
        {column: "body", label: "Body ", filterText: "Body ", required: true,
          visibleIf: (formData) => {
            return Array.isArray(formData.category) && formData.category.some(item => item.value === "Body");
          }
        },
        {column: "comment", label: "Comment", filterText: "Comment"},
        {column: "progress", label: "Progress", "filterText" : "Progress", visibleIf: (formData) => {return false;}}, 
    ],
    priority: ["done", "vehicle_type", "plate_no"],
    hidden: ["id", "schedule"],
    toRemove: ["customer", "alt_location", "username", "createdAt", "lastUpdated","vgtSelected"],
    exclude: ["vehicle_type", "plate_no", "category"],
    merge: [
    ],
    uneditable: ["vehicle_type", "plate_no"],
    dropdown: [
        "tk_no", "tk_no_spare"
    ],
    multiselect: [
      "category", "engine_", "drive_train", "chasis", "electrical_body", "ac_system", "repair_electrical", "defact", "body"
    ],
    checkbox: [
      "done", "spare_exists", "deleted"
    ],
    date: [
    ],
    datetime: [
      "down_since", "estimated_return"
    ],
    number: [
      "km",
    ],
});

const formData = ref({});
onMounted(() => {
  fields.value.columns.forEach(col => {
    formData.value[col.column] = col.default !== undefined ? col.default : '';
  });
  formData.value["username"] = userData.username;
});
</script>

<template>
  <div>
    <MiniForm
    v-if="settings.canInsert"
      v-model="formData"
      :fields="fields"
      :urls="urls"
    />
    <div class="card">
      <BigTable
        :settings="settings"
        :urls="urls"
        :fields="fields"
        :canInsert="false"
        :canUpdate="true"
    ></BigTable>
    </div>
  </div>
</template>

<style scoped>
.card {
  table-layout: fixed;
  margin: 40px auto 30px auto;
  width: 85%;
  padding: 40px;
}
  .card h3 {
    font-size: 28px;
  }
  </style>
