<script setup>
import { ref, watch, onMounted, computed } from 'vue';
import Multiselect from "vue-multiselect";
import "vue-multiselect/dist/vue-multiselect.css";
import axios from 'axios';
import { log } from '@/utils/logger';

import { useUserStore} from "@/stores/user.js";
import { API_BASE_URL } from '@/config/config';
const userData = useUserStore();

const props = defineProps({
  modelValue: {
    type: Object,
    default: () => ({}), // Make sure it's defaulting to an empty object
  },
  fields: Object,     // same as MiniTable
  urls: Object,       // accept urls for data and dropdowns
});

const emit = defineEmits(['update:modelValue']);

const formData = ref({ ...props.modelValue });
const dropdownData = ref({});
const dropdownsLoaded = ref(false);


// Fetch dropdown data when component is mounted
const fetchDropdowns = async () => {
  const fetchPromises = Object.entries(props.urls.dropdowns).map(async ([key, dropdown]) => {
    try {
      const response = await axios.get(dropdown.url);
      dropdownData.value[key] = response.data.map(item => ({
        label: item,
        value: item
      }));
    } catch (error) {
      console.error(`Failed to fetch dropdown data for ${key}:`, error);
    }
  });

  await Promise.all(fetchPromises);
  dropdownsLoaded.value = true;
};

onMounted(async () => {
  await fetchDropdowns();
});


watch(() => props.modelValue, (val) => {
  formData.value = { ...val };
});

const updateField = async (key, value) => {
  formData.value[key] = value;
  // console.log("Key : ", key, " val : ", formData.value[key]);
  emit('update:modelValue', { ...formData.value });

  props.fields.columns.forEach(async (field) => {
    if (field.autofill && field.autofill.triggeredBy === key) {
      try {
        const res = await axios.get(API_BASE_URL + "/" + field.autofill.url, {
          params: [value.label]
        });
        
        // Extract and update the autofilled field
        const filledValue = field.autofill.getValue(res.data);
        formData.value[field.column] = filledValue;
        emit('update:modelValue', { ...formData.value });
      } catch (err) {
        console.error(`Failed to autofill ${field.column}:`, err);
        formData.value[field.column] = ""; // Optional fallback
      }
    }
  });
};


const isUneditable = (col) => props.fields.uneditable?.includes(col.column);
const isType = (type, col) => props.fields[type]?.includes(col.column);

// Submit form data to the data URL
const submitForm = async () => {
  try {
    const payload = {};

    for (const field of props.fields.columns || []) {
      const key = field.column;

      if (props.fields.toRemove?.includes(key)) continue;
      if (props.fields.exclude?.includes(key)) continue;

      if (field.compute) {
        payload[key] = field.compute(formData.value);
        if(typeof payload[key] === 'boolean') payload[key] = payload[key] ? 1 : 0;
        continue;
      }
      const isVisible = typeof field.visibleIf === 'function'
        ? field.visibleIf(formData.value)
        : true;
      if (!isVisible) continue;

      const val = formData.value[key];

      const isRequired = field.required ?? false;
      const isEmpty =
      val === undefined ||
      val === null ||
      (typeof val === 'string' && val.trim() === '') ||
      (Array.isArray(val) && val.length === 0 && typeof val !== 'boolean');

      // Required field validation
      if (isRequired && isEmpty) {
        alert(`Field "${field.label || key}" is required.`);
        return;
      }
      if (!isRequired && isEmpty) continue

      // Multiselect (array of objects or strings)
      if (Array.isArray(val)) {
        payload[key] = val.map(v => (v?.label ?? v)).join(',');
      }
      // Single dropdown object
      else if (typeof val === 'object' && 'label' in val) {
        payload[key] = val.label;
      }
      // Primitive value
      else {
        if(typeof val === "boolean") payload[key] = val ? 1 : 0;
        else payload[key] = val;
      }
    }

    payload.username = userData.username;
    console.log("payload:", payload);

    const response = await axios.post(props.urls.data, payload);
    window.location.reload();
    console.log('Form submitted successfully:', response.data);
  } catch (error) {
    console.error('Failed to submit form:', error);
  }
};



const resetForm = () => {
  fields.value.columns.forEach(col => {
    formData.value[col.column] = col.default || '';  // Use default value or empty string
  });
};


const visibleColumns = computed(() =>
  props.fields.columns.filter(col =>
    typeof col.visibleIf === "function" ? col.visibleIf(formData.value) : true
  )
);
const previouslyVisible = ref(visibleColumns.value);

// Clear formData when a column becomes invisible
watch(visibleColumns, (newVisible) => {
  const currentlyVisible = newVisible.map(col => col.column);
  const noLongerVisible = previouslyVisible.value.filter(col => !currentlyVisible.includes(col));
  noLongerVisible.forEach(col => {
    updateField(col, null); // Or undefined/null if preferred
  });

  previouslyVisible.value = currentlyVisible;
}, { immediate: true });


</script>

<template>
  <div class="container-global mb-4">
  <div v-if="dropdownsLoaded">
    <form @submit.prevent="submitForm" class="mini-form">
      <div class="form-grid">
        <template v-for="(col, index) in visibleColumns" :key="col.column">
          <div class="form-field">
            <label>{{ col.label }}</label>

            <!-- Uneditable field -->
            <input
              v-if="isUneditable(col)"
              type="text"
              class="input bordered"
              :value="formData[col.column]"
              disabled
            />

            <!-- DateTime field -->
            <input
              v-else-if="isType('datetime', col)"
              type="datetime-local"
              class="input bordered"
              :value="formData[col.column] || ''"
              @input="e => updateField(col.column, e.target.value)"
            />

            <input
              v-else-if="isType('number', col)"
              type="number"
              class="input bordered"
              :value="formData[col.column] || ''"
              @input="e => updateField(col.column, e.target.value)"
            />

            <!-- Dropdown field -->
            <Multiselect
              v-else-if="isType('dropdown', col)"
              v-model="formData[col.column]"
              :options="dropdownData[col.column] || []"
              placeholder="Select"
              label="label"
              track-by="value"
              class="multiselect no-border"
              @update:modelValue="value => updateField(col.column, value)"
            />
            <!-- Dropdown field -->
            <Multiselect
              v-else-if="isType('multiselect', col)"
              v-model="formData[col.column]"
              :options="dropdownData[col.column] || []"
              placeholder="Select"
              label="label"
              track-by="value"
              class="multiselect no-border"
              :class="{ 'multiselect--multiple': isType('multiselect', col) }"
              multiple="true"
              :close-on-select="false"
              @update:modelValue="value => updateField(col.column, value)"
            />
            <!-- Checkbox field -->
            <div v-else-if="isType('checkbox', col)" class="checkbox-field no-border">
              <input
                type="checkbox"
                :checked="formData[col.column]"
                @change="e => updateField(col.column, e.target.checked)"
                class="form-checkbox"
              />
            </div>

            <!-- Text field (default) -->
            <input
              v-else
              type="text"
              class="form-input bordered"
              v-model="formData[col.column]"
              :placeholder="col.filterText || col.label"
            />
          </div>
        </template>
      </div>

      <div class="submit-container">
        <button type="submit" class="submit-button bordered">Submit</button>
      </div>
    </form>
  </div>

  <div v-else class="loading-message">
  <i class="pi pi-spin pi-spinner" style="font-size: 1.5rem;"></i>
  <p>Loading form...</p>
  </div>
  </div>
</template>


<style scoped>
/* Base border style */
.bordered {
  border: 1px solid #d3d3d3 !important;
  border-radius: 4px;
}

/* Hilangkan border untuk Multiselect */
.multiselect.no-border {
  border: none !important;
  box-shadow: none !important;
  padding-left: 0;
  padding-right: 0;
}

/* Optional: Atur ulang tampilan bagian dalam jika ingin lebih clean */
.multiselect.no-border .multiselect__tags {
  border: none !important;
  padding-left: 0;
  padding-right: 0;
 
}

.multiselect.no-border .multiselect__select {
  display: none; /* sembunyikan ikon dropdown panah jika mau minimalis */
}

.mini-form {
  width: 100%;
  margin: 0 auto;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  width: 100%;
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
}

.form-field label {
  font-weight: 500;
  font-size: 14px;
}

.form-input,
.multiselect {
  width: 100%;
  padding: 10px 12px;
  font-size: 14px;
  transition: border-color 0.2s;
  height: 40px;
  box-sizing: border-box;
}

.form-input:focus {
  outline: none;
  border-color: #db0000;
  box-shadow: 0 0 0 2px rgba(219, 0, 0, 0.1);
}

/* Style khusus untuk Multiselect */
.multiselect {
  min-height: 40px;
  padding-top: 0;
  padding-bottom: 0;
}

/* Style untuk checkbox */
.checkbox-field {
  display: flex;
  align-items: center;
  gap: 8px;
  height: 40px;
  padding: 10px 0;
}

.form-checkbox {
  width: 18px;
  height: 18px;
  accent-color: #db0000;
}

/* Style untuk tombol submit */
.submit-container {
  margin-top: 24px;
  text-align: right;
}

.submit-button {
  padding: 10px 24px;
  background-color: #db0000;
  color: white;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: background-color 0.2s;
}

.submit-button:hover {
  background-color: #ff2424;
}

/* Style untuk loading state */
.loading-message {
  text-align: center;
  padding: 20px;
  color: #252525;
}

.input {
  padding-left: 0;
  padding-right: 0;
  height: 40px;
  background: transparent;
  padding: 10px 12px;
  font-size: 14px;
}

/* Card border 
.card {
  border: 1px solid #d3d3d3;
  table-layout: fixed;
  width: 100%;
}*/

/* Multiselect multiple mode should expand properly */
.multiselect.no-border.multiselect--multiple {
  min-height: auto !important;
  height: auto !important;
  padding-top: 6px;
  padding-bottom: 6px;
}

/* Make tags wrap properly and avoid overlapping */
.multiselect--multiple .multiselect__tags {
  display: flex;
  flex-wrap: wrap;
  min-height: 40px;
  padding: 4px 8px;
  gap: 4px;
}

/* Ratakan isi Multiselect ke tengah */
.multiselect .multiselect__single,
.multiselect .multiselect__tags {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* Hilangkan padding tambahan di wrapper */
.multiselect.no-border {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
</style>